input {
	width: 60%;
	padding: 12px 24px;
	-webkit-transition: -webkit-transform 250ms ease-in-out;
	transition: -webkit-transform 250ms ease-in-out;
	transition: transform 250ms ease-in-out;
	transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
	letter-spacing: 4px;
	line-height: 18px;
	border: none;
	border-bottom: 1px solid black;
	color: #575756;
	background-color: transparent;
	font-size: 10px;
	text-align: center;

	margin-left: 15%;
}

.custom-btn {
	padding: 10px 25px;
	font-family: "Roboto", sans-serif;
	font-weight: 500;
	margin-top: 10px;
	margin-left: 15%;
	background: transparent;
	outline: none !important;
	cursor: pointer;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
	position: relative;
	display: inline-block;
}

.btn-4 {
	width: 130px;
	height: 40px;
	line-height: 42px;
	padding: 0;
	border: none;
}

.btn-4 span {
	position: relative;
	display: block;
	width: 100%;
	margin-left: -%;
	height: 100%;
}

.btn-4:before,
.btn-4:after {
	position: absolute;
	content: "";
	right: 0;
	top: 0;
	background: #000;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.btn-4:before {
	height: 50%;
	width: 2px;
}

.btn-4:after {
	width: 20%;
	height: 2px;
}

.btn-4:hover:before {
	height: 100%;
}

.btn-4:hover:after {
	width: 100%;
}

.btn-4 span:before,
.btn-4 span:after {
	position: absolute;
	content: "";
	left: 0;
	bottom: 0;
	background: #000;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.btn-4 span:before {
	width: 2px;
	height: 50%;
}

.btn-4 span:after {
	width: 20%;
	height: 2px;
}

.btn-4 span:hover:before {
	height: 100%;
}

.btn-4 span:hover:after {
	width: 100%;
}


.dropbtn {
	background-color: #FDE6C6;
	padding: 16px;
	font-size: 16px;
	border: none;
	cursor: pointer;
}

.dropdown {
	position: relative;
	display: inline-block;
	float:right;
}

.dropdown-content {
	display: none;
	position: absolute;
	right: 0;
	background-color: #FDE6C6;
	min-width: 160px;
	box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
	z-index: 1;
}

.dropdown-content a {
	color: black;
	padding: 12px 16px;
	text-decoration: none;
	display: block;
}

.dropdown-content a:hover {
	background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
	display: block;
}

.dropdown:hover .dropbtn {
	background-color: brown;
}

